body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  text-align: center;
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

document {
  font-size: 12px; }

.division-box {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 10px;
  grid-column-gap: 10px;
  min-width: 800px;
  max-width: 1100px;
  padding: 10px;
  margin: auto; }
  .division-box > * {
    max-width: 300px; }

.choices {
  grid-column-start: 1;
  grid-row-start: 1; }

.support {
  grid-column-start: 1;
  grid-row-start: 2; }

.basics {
  grid-column-start: 2;
  grid-row-start: 1; }

.equipment {
  grid-column-start: 2;
  grid-row-start: 2; }

.combat {
  grid-column-start: 3;
  grid-row-start: 1; }

.terrain {
  grid-column-start: 3;
  grid-row-start: 2; }

@media (max-width: 900px) {
  .division-box {
    grid-template-columns: 1fr 1fr;
    min-width: inherit; }
  .choices {
    grid-row-start: 1;
    grid-column-start: 1; }
  .support {
    grid-row-start: 1;
    grid-column-start: 2; }
  .basics {
    grid-row-start: 2;
    grid-column-start: 1; }
  .equipment {
    grid-row-start: 2;
    grid-column-start: 2; }
  .combat {
    grid-row-start: 3;
    grid-column-start: 1; }
  .terrain {
    grid-row-start: 3;
    grid-column-start: 2; } }

@media (max-width: 600px) {
  .division-box {
    grid-template-columns: 1fr;
    min-width: inherit; }
  .choices {
    grid-row-start: 1;
    grid-column-start: 1; }
  .support {
    grid-row-start: 2;
    grid-column-start: 1; }
  .basics {
    grid-row-start: 3;
    grid-column-start: 1; }
  .equipment {
    grid-row-start: 4;
    grid-column-start: 1; }
  .combat {
    grid-row-start: 5;
    grid-column-start: 1; }
  .terrain {
    grid-row-start: 6;
    grid-column-start: 1; } }

.bonus {
  color: #080; }

.penalty {
  color: #800; }

.support .list {
  -webkit-columns: 2;
          columns: 2; }

.support label {
  margin: 0; }

.disabled_company {
  color: #888; }

.division-box ul {
  display: block;
  margin: 0;
  padding: 0; }

.division-box li {
  margin: 0;
  padding: 0;
  width: 100%; }

.division-box .terrain > table > thead > tr > th {
  text-align: center; }

.division-box .terrain > table > tbody > tr > td {
  width: 25%;
  text-align: center; }

.unit-select {
  display: -webkit-flex;
  display: flex; }

input, select {
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #fff;
  color: #495057; }

input[type=text] {
  width: 100%;
  max-width: 300px; }

input[type=number] {
  width: 60px;
  margin-right: 5px;
  text-align: right; }

select {
  width: 100%;
  max-width: 300px;
  border-radius: 5px;
  height: 28px;
  border: 1px solid #ced4da; }

/* Shouldn't bootstrap handle this? */
.checkbox label input {
  margin-right: 5px; }

.checkbox.disabled label {
  color: #ccc; }

.mod-selector {
  text-align: center; }
  .mod-selector button {
    margin: 5px; }

.stat {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between; }
  .stat:hover .stat-name {
    font-weight: bold; }

.stat-tooltip {
  position: absolute;
  padding: 5px;
  top: 25px;
  right: 0px;
  background-color: white;
  border: 1px solid black;
  z-index: 1;
  display: none; }

.stat:hover .stat-tooltip {
  display: block; }

.stat-tooltip table {
  margin-left: auto; }

.equipment-list li {
  display: block;
  display: block; }

.equipment-list .equipment-count {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

.equipment-list .upgrade {
  text-align: right; }
  .equipment-list .upgrade input {
    margin-left: 5px; }

.substat-value {
  text-align: right; }

.terrain-cell {
  position: relative; }
  .terrain-cell:hover .stat-value {
    font-weight: bold; }
  .terrain-cell:hover .stat-tooltip {
    display: block;
    text-align: left; }
  .terrain-cell .stat-tooltip div {
    white-space: nowrap; }

.substat-name {
  white-space: nowrap; }

.save-and-load-controls {
  text-align: center; }
  .save-and-load-controls button {
    margin: 5px; }

